<template>
  <div
    class="card"
    :class="{ disable: isDisable }"
    :style="{
      height: `${(685 - 16 * 4) / Math.sqrt(cardsContext.length) - 16}px`,
      width: `${(((685 - 16 * 4) / Math.sqrt(cardsContext.length) - 16) * 3) / 4}px`,
    }"
  >
    <div
      class="card__inner"
      :class="{
        'is-flipped': isFlipped,
      }"
      @click="onToggleFlipCard()"
    >
      <div class="card__face card__face--front">
        <div
          class="card__content"
          :style="{
            backgroundSize: `${
              (((685 - 16 * 4) / Math.sqrt(cardsContext.length) - 16) * 3) / 4 / 3
            }px ${(((685 - 16 * 4) / Math.sqrt(cardsContext.length) - 16) * 3) / 4 / 3}px`,
          }"
        ></div>
      </div>
      <div class="card__face card__face--back">
        <div
          class="card__content"
          :style="{ backgroundImage: `url(${require('@/assets/' + imageBackUrl)})` }"
        ></div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    imageBackUrl: {
      type: String,
      required: true,
    },
    card: {
      type: [String, Number, Array, Object],
      required: true,
    },
    cardsContext: {
      type: [Array, String, Number, Object],
      required: true,
    },
  },
  data() {
    return {
      isFlipped: false,
      isDisable: false,
    };
  },
  methods: {
    onToggleFlipCard() {
      if (this.isDisable) return false;
      if (this.isFlipped) return false;
      this.isFlipped = !this.isFlipped;
      if (this.isFlipped) this.$emit("onFlip", this.card);
    },
    onResetFlip() {
      this.isDisable = false;
      this.isFlipped = false;
      // console.log(this.isFlipped);
    },
    onDisableFlip() {
      this.isDisable = true;
    },
  },
};
</script>

<style lang="css" scoped>
.card {
  display: inline-block;
  margin-right: 0.5rem;
  margin-bottom: 0.5rem;
}

.card__inner {
  width: 100%;
  height: 100%;
  transition: 1s;
  transform-style: preserve-3d;
  cursor: pointer;
  position: relative;
}

.card__inner.is-flipped {
  transform: rotateY(-180deg);
}

.card__face {
  position: absolute;
  width: 100%;
  height: 100%;
  backface-visibility: hidden;
  overflow: hidden;
  border-radius: 1rem;
  padding: 1rem;
  box-shadow: 0 3px 10px 3px rgba(0, 0, 0, 0.3);
}

.card__face--front .card__content {
  background: url("../assets/images/icon_back.png") no-repeat center center;
  height: 100%;
  width: 100%;
}

.card__face--back {
  background-color: var(--light);
  transform: rotateY(180deg);
}

.card__face--back .card__content {
  background-size: contain;
  background-position: center center;
  background-repeat: no-repeat;
  height: 100%;
  width: 100%;
}
</style>
