<template>
  <main-screen v-if="statusMatch === 'main'" @onStart="onHandleBeforeStart($event)" />
  <interact-screen
    v-if="statusMatch === 'match'"
    :cardsContext="settings.cardsContext"
    @onFinish="onShowResult()"
  />
  <result-screen
    v-if="statusMatch === 'result'"
    :timer="timer"
    @onStartAgain="statusMatch = 'main'"
  />
</template>

<script>
import MainScreen from "./components/MainScreen.vue";
import InteractScreen from "./components/InteractScreen.vue";
import ResultScreen from "./components/ResultScreen.vue";
import { shuffle } from "./utils/array";

export default {
  name: "App",
  components: {
    MainScreen,
    InteractScreen,
    ResultScreen,
  },
  data() {
    return {
      statusMatch: "main",
      settings: {
        totalBlocks: 0,
        cardsContext: {},
        startedAt: null,
      },
      timer: 0,
    };
  },
  methods: {
    onHandleBeforeStart(config) {
      this.settings.totalBlocks = config.totalOfBlock;
      const firstCards = Array.from(
        {
          length: this.settings.totalBlocks / 2,
        },
        (_, i) => i + 1
      );
      const cards = [...firstCards, ...firstCards];
      this.settings.cardsContext = shuffle(shuffle(shuffle(cards)));
      this.settings.startedAt = new Date().getTime();
      this.statusMatch = "match";
    },
    onShowResult() {
      this.timer = new Date().getTime() - this.settings.startedAt;
      this.statusMatch = "result";
    },
  },
};
</script>

