<template>
  <div class="screen">
    <div
      class="screen__inner"
      :style="{
        width: `${
          ((((685 - 16 * 4) / Math.sqrt(cardsContext.length) - 16) * 3) / 4 + 16) *
          Math.sqrt(cardsContext.length)
        }px`,
      }"
    >
      <card-flip
        v-for="(card, index) in cardsContext"
        :key="index"
        :ref="`card-${index}`"
        :imageBackUrl="`images/${card}.png`"
        :card="{ index, value: card }"
        :cardsContext="cardsContext"
        @onFlip="checkRule($event)"
      />
    </div>
  </div>
</template>

<script>
import CardFlip from "./Card.vue";
export default {
  props: {
    cardsContext: {
      type: Array,
      default: function () {
        return [];
      },
    },
  },
  components: {
    CardFlip,
  },
  data() {
    return {
      rules: [],
      rights: [],
    };
  },
  methods: {
    checkRule(card) {
      if (this.rules.length === 2) {
        for (let index = 0; index < this.cardsContext.length; index++) {
          const element = this.$refs[`card-${index}`];
          element[0].onDisableFlip();
        }
        return false;
      }
      this.rules.push(card);
      if (this.rules.length === 2 && this.rules[0].value === this.rules[1].value) {
        this.$refs[`card-${this.rules[0].index}`][0].onDisableFlip();
        this.$refs[`card-${this.rules[1].index}`][0].onDisableFlip();
        this.rights.push(...this.rules);
        this.rules = [];
      } else if (this.rules.length === 2 && this.rules[0].value !== this.rules[1].value) {
        setTimeout(() => {
          for (let index = 0; index < this.cardsContext.length; index++) {
            const cardTrue = this.rights.find((v) => {
              // console.log(v.index, index);
              return v.index === index;
            });
            if (!cardTrue) {
              this.$refs[`card-${index}`][0].onResetFlip();
              // console.log(cardTrue);
            }
            // console.log(element[0]);
            // if (!eTrue) {
            // element[0].onResetFlip();
            // }
          }
          this.rules = [];
        }, 800);
      }
      if (this.rights.length / 2 === this.cardsContext.length / 2) {
        setTimeout(() => {
          this.$emit("onFinish", {});
        }, 920);
      }
    },
  },
};
</script>

<style scoped lang="css">
.screen {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;
  background-color: var(--dark);
  color: var(--light);
}

.screen__inner {
  display: flex;
  flex-wrap: wrap;
  margin: 2rem auto;
}
</style>
